'use strict';

function HttpService($http) {

  return {

    setData: function (response) {
      angular.extend(this, response);
    },

    load: function (endurl, customheaders, dataToPass, args, method) {
      return $http({
        url: endurl,
        method: 'GET'
      }).then(function (response, status, headers, config) {
        return response;
      }, function (response, status, headers, config) {
        return false;
      });
    },

    delete: function (url, id) {
      $http.delete(url + '/' + id);
    },

    update: function (url, data) {

      $http.put(url, data)
        .success(function (data, status, headers, config) {
          console.log("PUT SUCCESS" + data + status + headers + config);
        })
        .error(function (data, status, headers, config) {
          console.log("PUT ERROR" + data + status + headers + config);
        });
    },
    patch: function (url, paramsValues) {
      $http({
        url: url+'/'+paramsValues.id,
        method: 'PATCH',
        params: paramsValues
      })
        .success(function (response) {
          console.log(response);
        }).
      error(function (response) {
        console.log(response);
        return false;
      });
    }
  };
}

(function() {
  'use strict';

  angular.module('angularChallenge', ['ngRoute']);

})();

(function() {
  'use strict';

  angular
    .module('angularChallenge')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController() {
    var vm = this;
    vm.title = "Welcome to the AngularJS Code Challenge";
    vm.description = "This is the boilerplate project for the Angular JS challenge";
  }
})();

'use strict';

function MainController($scope, $state, $rootScope, $window, $stateParams, HttpService) {


  $scope.init = function() {
      HttpService.load("http://0.0.0.0:3000/api/custdetails")
        .then(function(response) {
          if (response) {
            console.log(JSON.stringify(response.data));
            $scope.showAllData = response.data;
          }
        }, function(error) {
          console.log("Error occurred");
        });
    };

$scope.addMoreData = function(){
  var data = {
    cust_name: $scope.custNameModel,
    phone_number: $scope.phoneNumberModel,
    id: $scope.idModel
  };

  HttpService.update('http://0.0.0.0:3000/api/custdetails?', data);
  $scope.init();

};

$scope.updateData = function(){
  var data = {
    cust_name: $scope.custNameModel,
    phone_number: $scope.phoneNumberModel,
    id: $scope.idModel
  };

  HttpService.patch('http://0.0.0.0:3000/api/custdetails?', data);
  $scope.init();

};

$scope.deleteData = function(){
  console.log("ID defined is: "+$scope.idModel);
  HttpService.delete("http://0.0.0.0:3000/api/custdetails", $scope.idModel);
  $scope.init();

};





}

(function() {
  'use strict';

  angular
    .module('angularChallenge')
    .config(routeConfig)
    .service('HttpService',HttpService)
    .controller('MainController', MainController)

  function routeConfig($routeProvider) {
    $routeProvider
      .when('/', {
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'vm'
      })
      .otherwise({
        redirectTo: '/'
      });
  }

})();

